import { header, indexPage, deskMenu } from '../elements';
import { throttliing } from '../function';

if (indexPage.section !== null) {
    // deskMenu.stickyMenu.style.visibility = 'hidden';
    // deskMenu.IndexPageMenu.style.visibility = 'visible';

    const scrollHandler = () => {
        const sectionTop = indexPage.section.getBoundingClientRect().top;
        const deskMenuTop = deskMenu.IndexPageMenu.getBoundingClientRect().top;

        if (sectionTop < 0) {
            header.self.classList.add('active');
        }

        if (sectionTop >= 0) {
            header.self.classList.remove('active');
        }

        if (deskMenuTop < 60) {
            deskMenu.IndexPageMenu.style.visibility = 'hidden';
            deskMenu.stickyMenu.classList.add('active');
        }

        if (deskMenuTop >= 60) {
            deskMenu.IndexPageMenu.style.visibility = 'visible';
            deskMenu.stickyMenu.classList.remove('active');
        }
    };

    window.addEventListener('scroll', throttliing(scrollHandler, 300));
}

if (indexPage.section === null) {
    header.self.style.backgroundColor = 'rgba(226, 231, 225, 1)';
}
